$s-circle-total-width: 248px;
$s-circle-total-height: 154px;
$s-circle-image: '../img/sprite/sprite.png';
$s-circle: (0px, 0px, 0px, 0px, 94px, 91px, 248px, 154px, '../img/sprite/sprite.png', 's-circle', );
